/* 容器 */
.scrolling-container {
  width: 100%;               /* 容器宽度为100% */
  overflow: hidden;          /* 隐藏超出容器的部分 */
  white-space: nowrap;       /* 防止文字换行 */
  position: relative;   
  padding: 0 20px;       /* 设置相对定位 */
}

/* 滚动文字 */
.scrolling-text {
  display: inline-block;     /* 让文字横向排列 */
  white-space: nowrap;       /* 防止文字换行 */
  animation: scroll-left 10s linear infinite;  /* 设置滚动动画 */
}

/* 定义滚动动画 */
@keyframes scroll-left {
  0% {
    transform: translateX(100%); /* 文字从屏幕外右侧开始 */
  }
  100% {
    transform: translateX(-100%); /* 文字滚动到屏幕外左侧 */
  }
}